<template>
  <v-app>
    <v-navigation-drawer 
      v-model="drawer"
      app 
      clipped
    >
      <v-list>
        <v-list-item>
          <v-list-item-content>
            <v-list-item-title class="title">
              Tyler Nullmier's <br> Online Portfolio
            </v-list-item-title>
            <!-- <v-list-item-subtitle class="ma-4 text-wrap">
              This is the subtitle
            </v-list-item-subtitle> -->
          </v-list-item-content>
        </v-list-item>
        <v-divider class="my-2"></v-divider>
        <!-- Create a link for each route in routes. If the route has childern, 
        create a list group and display the childern in the list group -->
        <template v-for="route of routes">
          <v-list-item 
            v-if="!route.children"
            :key="route.name"
            link 
            :to="route.path"
            color="white"
          >
            <v-list-item-icon>
              <v-icon>
                {{ route.meta.icon }}
              </v-icon>
            </v-list-item-icon>
            <v-list-item-content>
              <v-list-item-title>
                {{ route.name }}
              </v-list-item-title>
            </v-list-item-content>
          </v-list-item>
          <v-list-group v-else :key="route.name" color="white">
            <template v-slot:activator>
              <v-list-item-icon>
                <v-icon>
                  {{ route.meta.icon }}
                </v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  {{ route.name }}
                </v-list-item-title>
              </v-list-item-content>
            </template>
            <v-list-item 
              v-for="childRoute of route.children"
              :key="childRoute.name"
              link 
              :to="[route.path, childRoute.path].join('/')"
              class="pl-10"
            >
              <v-list-item-icon>
                <v-icon>
                  {{ childRoute.meta.icon }}
                </v-icon>
              </v-list-item-icon>
              <v-list-item-content>
                <v-list-item-title>
                  {{ childRoute.name }}
                </v-list-item-title>
              </v-list-item-content>
            </v-list-item>
          </v-list-group>
      </template>
      </v-list>
    </v-navigation-drawer>
    <v-app-bar
      app
      clipped-left
      class="light-blue darken-4"
    >
      <v-app-bar-nav-icon @click.stop="drawer = !drawer" />
      
      <v-toolbar-title>
        {{ $route.name }}
      </v-toolbar-title>
      
      <v-spacer></v-spacer>

    </v-app-bar>

    <v-main>
      <router-view/>
      <v-footer
        padless
      >
        <v-card
          flat
          tile
          class="light-blue darken-4 white--text text-center"
          style="width: 100%;"
        >
          <v-card-text class="white--text">
            Connect with me on social media!
          </v-card-text>

          <v-card-text class="pt-0">
            <v-btn
              class="mx-4 white--text"
              icon
            >
              <v-icon size="24px">
                mdi-linkedin
              </v-icon>
            </v-btn>
          </v-card-text>

          <v-divider></v-divider>

          <v-card-text class="white--text">
            {{ new Date().getFullYear() }} — <strong>Tyler Nullmeier</strong>
          </v-card-text>
        </v-card>
      </v-footer>
    </v-main>
  </v-app>
</template>

<script>
export default {
  name: 'App',
  data: () => ({
    // This varaible keeps track of the state of the navigation drawer
    drawer: null
  }),
  computed: {
    routes() {
      // return a list of all routes that are not supposed to be hidden
      return this.$router.options.routes.filter(r => !r.meta.hidden);
    },
  },
  async created() {
    // enable the dark theme by default
    this.$vuetify.theme.dark = true;
  }
};
</script>
