import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import Information from '@/information.js'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      icon: 'mdi-home-circle-outline',
    }
  },
  {
    path: '/documents/resume-nophone.pdf',
    name: 'Resume',
    meta: {
      isStatic: true,
      icon: 'mdi-file-document-outline',
    }
  },
  {
    path: '/soft-dev',
    name: 'Software',
    component: () => import(/* webpackChunkName: "softdev" */ '../views/SoftDev/Index.vue'),
    meta: {
      icon: 'mdi-code-braces',
    },
    children: [
      {
        path: 'transact-games',
        name: 'Transact Games',
        component: () => import(/* webpackChunkName: "transact-games" */ '../views/SoftDev/TransactGames.vue'),
        meta: { 
          icon: 'mdi-view-carousel' 
        },
      },
      {
        path: 'uniforms-elite',
        name: 'Uniforms Elite',
        component: () => import(/* webpackChunkName: "transact-games" */ '../views/SoftDev/UniformsElite.vue'),
        meta: { 
          icon: 'mdi-bow-tie' 
        },
      },
      {
        path: 'iotscp',
        name: 'IOTSCP',
        component: () => import(/* webpackChunkName: "transact-games" */ '../views/SoftDev/IOTSCP.vue'),
        meta: { 
          icon: 'mdi-monitor' 
        },
      },
      {
        path: 'fuzzysort',
        name: 'Fuzzysort',
        component: () => import(/* webpackChunkName: "transact-games" */ '../views/SoftDev/Fuzzysort.vue'),
        meta: { 
          icon: 'mdi-sort-numeric-variant' 
        },
      }
    ],
  },
  {
    path: '/management',
    name: 'Management',
    component: () => import(/* webpackChunkName: "softdev" */ '../views/SoftDev/Index.vue'),
    meta: {
      icon: 'mdi-clipboard-text-outline',
    },
    children: []
  },
  {
    path: '/design',
    name: 'Design',
    component: () => import(/* webpackChunkName: "softdev" */ '../views/SoftDev/Index.vue'),
    meta: {
      icon: 'mdi-palette',
    },
    children: []
  }
  // {
  //   path: '/about',
  //   name: 'About',
  //   // route level code-splitting
  //   // this generates a separate chunk (about.[hash].js) for this route
  //   // which is lazy-loaded when the route is visited.
  //   component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
  //   meta: {
  //     icon: 'mdi-information-outline',
  //   }
  // }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

router.beforeEach(function(to, from, next) {
  window.scroll(0, 0);
  if(to.meta.isStatic) {
    console.log(to);
    document.location = to.path;
  } else {
    next();
  }
});

export default router
