<template>
  <span>
    <div class="text-h2 text-center my-5 py-5">
      {{ title }}
    </div>
    <v-timeline :dense="$vuetify.breakpoint.smAndDown">
      <v-timeline-item
        v-for="(item, i) in items"
        :key="i"
        :color="item.color"
        small
      >
        <template v-slot:opposite>
          <span
            :class="`headline font-weight-bold ${getTextColor(item)}`"
            v-text="item.year"
          ></span>
        </template>
        <div class="py-4">
        <h2 :class="`headline font-weight-light mb-4 ${getTextColor(item)}`">
          <span v-if="$vuetify.breakpoint.smAndDown">{{ item.year }}: </span>{{ item.title }}
        </h2>
        <div>
          {{ item.description }}
        </div>
        <div v-if="item.action" class="mt-5">
          <v-btn v-on:click="item.action.thunk(self)" color="info" text>
            {{ item.action.text }}
          </v-btn>
        </div>
      </div>
      </v-timeline-item>
    </v-timeline>
  </span>
</template>

<script>
  export default {
    props: ['title', 'items'],
    data() {
      return { self: this };
    },
    methods: {
      getTextColor(item) {
        return item.textColor ? item.textColor : `${item.color}--text`;
      }
    }
  }
</script>